.promo {
  text-align: center;
}

.promo__block {
  width: 320px;
  height: 302px;
  background:
  linear-gradient( rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)),
  url(../../img/cat-background.png);
  background-repeat: no-repeat;
  margin-bottom: 138px;

}

.promo__title {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 36px;
  text-align: center;
  color: #FFFFFF;
  margin: 0;

}

.promo__text {
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 14px;
  text-transform: uppercase;
  color: #FFFFFF;
  margin: 0;
}

.promo__link {
  display: block;
  font-family: "oswald", sans-serif;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  text-transform: uppercase;
  background-color: #68B738;
  width: 280px;
  padding: 7px 0;
  text-align: center;

}
